.friendList {
  /* display: inline-block; */

  list-style: none;
  padding-left: 0;
  margin-top: 50px;
  margin-bottom: 0;
}

.item {
  width: 300px;
  height: 80px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 1), -23px 0 20px -23px rgba(0, 0, 0, 0.8),
    23px 0 20px -23px rgba(0, 0, 0, 0.8), 0 0 40px rgba(0, 0, 0, 0.1) inset;

  margin-bottom: 15px;
  margin-left: auto;
  margin-right: auto;

  display: flex;
}

.statusOn {
  display: inline-block;
  width: 15px;
  height: 15px;

  margin-top: 5px;
  margin-left: 5px;

  background-color: green;
  border-radius: 50%;
}
.statusOff {
  display: inline-block;
  width: 15px;
  height: 15px;

  margin-top: 5px;
  margin-left: 5px;

  background-color: red;
  border-radius: 50%;
}

.avatar {
  margin-left: 25px;
}

.name {
  margin-left: 55px;
  margin-top: 0;
  margin-bottom: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  font-family: Nerko One;
  font-size: 40px;
}